<!--
  @auth:lcw
  
  @lyq id {{number}}
-->

<template>
  <div class="incNumber">{{number}}</div>
</template>

<script>
  export default {
    name: "index",
    props: {
      // 
      incNumber: {
        type: [Number, String],
        default() {
          return 0
        }
      }
    },
    data() {
      return {
        number: 0
      }
    },
    created() {
      this.init();
    },
    methods: {
      init() {
        this.NumAutoPlusAnimation({
          time: 3000,
          num: this.incNumber,
          regulator: 100
        });
      },
      //,
      NumAutoPlusAnimation(options) {
        options = options || {};

        let
          // $this = document.getElementById(targetEle),
          // time = options.time || $this.data('time'), //--
          time = options.time || 10, //--
          //  finalNum = options.num || $this.data('value'), //
          finalNum = options.num === undefined ? 0 : options.num, //
          //finalNum = options.num || 10, //
          regulator = options.regulator || 100, //regulator

          step = finalNum / (time / regulator), /*30ms--*/
          decimalSplit,
          decimalNum,
          count = 0, //
          initial = 0;

        if (finalNum % 1 !== 0) {
          decimalSplit = String(finalNum).split(".");
          decimalNum = decimalSplit[1].length;
        }

        let timer = setInterval(() => {
          count = count + step;

          if (count >= finalNum) {
            clearInterval(timer);
            count = finalNum;
          }

          //t
          //textDOM
          let t = 0;
          // 
          if (finalNum % 1 === 0) {
            t = Math.floor(count);
          } else {
            t = count.toFixed(decimalNum);
          }

          if (t === initial) return;

          initial = t;
          // 
          // let formatNumber = this.formatMoney(initial, 1);
          // $this.innerHTML = initial;
          this.number = initial;
        }, 50);
      },

      /*
       * formatMoney(s,type)
       * 
       * s.
       * type,0/1.
       * .
       */
      formatMoney(s, type) {
        if (/[^0-9\.]/.test(s))
          return "0.00";
        if (s == null || s == "null" || s == "")
          return "0.00";
        s = s.toString().replace(/^(\d*)$/, "$1.");
        s = (s + "00").replace(/(\d*\.\d\d)\d*/, "$1");
        s = s.replace(".", ",");
        var re = /(\d)(\d{3},)/;
        while (re.test(s))
          s = s.replace(re, "$1,$2");
        s = s.replace(/,(\d\d)$/, ".$1");
        if (type == 0) {
          var a = s.split(".");
          if (a[1] == "00") {
            s = a[0];
          }
        }
        return s;
      }
    },
    watch: {
      incNumber: {
        handler(newVal, oldVal) {
          this.number = newVal;
          this.init();
        },
        deep: true
      }
    }

  }
</script>

<style lang="stylus" scoped>
  .incNumber
    display inline-block
</style>
